export default {
  props: {
    max: {
      type: Number,
      default: 1
    },
    title: String
  },
  data() {
    return {
      files: [],
      httpUrls: []
    };
  },
  methods: {
    upload(item) {
      this.$http
        .post("/mall/v1/uploadFile", {
          file: item.src.split(",")[1],
          fileName: item.name
        })
        .then(data => {
          this.files.push(item);
          this.httpUrls.push(data);
          this.$emit("upload", this.httpUrls);
        })
        .catch(e => {
          this.$vux.toast.text(e);
        });
    },
    remove(index) {
      this.files.splice(index, 1);
      this.httpUrls.splice(index, 1);
      this.$emit("upload", this.httpUrls);
    },
    fileChanged() {
      let list = this.$refs.file.files,
        len = Object.keys(list).length;
      if (len > this.max) {
        this.$vux.toast.text(`最多只能上传${this.max}张`);
      }
      for (let i = 0; i < list.length; i++) {
        if (!this.isContain(list[i]) && i < this.max) {
          const item = {
            name: list[i].name,
            size: list[i].size,
            file: list[i]
          };
          this.html5Reader(list[i])
            .then(data => {
              item.src = data;
              this.upload(item);
            })
            .catch(e => {
              this.$vux.toast.text(e);
            });
        }
      }
      this.$refs.file.value = "";
    },
    // Promise 封装异步读取图片转base64
    html5Reader(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function() {
          let canvas = document.createElement("canvas"),
            ctx = canvas.getContext("2d"),
            image = new Image();
          image.src = this.result;
          image.onload = function() {
          let cw = image.width,
              ch = image.height,
               w = image.width,
               h = image.height;
            canvas.width = w;
            canvas.height = h;
            if (cw > 400 && cw > ch) {
              w = 400;
              h = 400 * ch / cw;
              canvas.width = w;
              canvas.height = h;
            }
            if (ch > 400 && ch > cw) {
              h = 400;
              w = 400 * cw / ch;
              canvas.width = w;
              canvas.height = h;
            }
            ctx.drawImage(image, 0, 0, w, h);
            let base64 = canvas.toDataURL("image/jpeg", 0.7);
            resolve(base64);
          };
        };
        reader.onerror = reject;
      });
    },
    isContain(file) {
      return this.files.find(
        item => item.name === file.name && item.size === file.size
      );
    }
  }
};



