
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'

export default {
name: "orderDetail",
components: {
ViewBox,
Group,
Cell,
Flow,
FlowState,
FlowLine
},
data(){
return{
detail:{}
}
},
created() {
let id = this.$route.query.id;
this.$http
.get(`/mall/v1/declaration/${id}`)
.then(data => {
this.detail = data.result;
})
.catch(e => {
this.$vux.toast.text(e);
});
},
methods:{

getStatus(state,range=[]){
let p = range.find(item=>{
return item === state;
})
return p+2?true:false;
}
},
};



