import Vue from 'vue'
import VueScroller from 'vue-scroller'
import App from './App'
import store from './store'
import router from './router'
import Http from './http'
import './main.less'




Vue.use(VueScroller)

Vue.config.productionTip = false;


import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)


const FastClick = require('fastclick')
FastClick.attach(document.body)


import './assets/js/rem'
window.document.addEventListener('touchstart', function (event) {
/* 解决 active兼容处理 即 伪类 :active 失效  */
}, false);


Vue.prototype.$http = Http;
if(window.location.hash.indexOf('addOrderOuter')<0){
router.push({path:'/home/addOrder',replace:true})
}

new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
});



