
import Uploader from "@/common/uploadImg.vue";
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

export default {
name: "AddOrder",
components: {
XInput,
Group,
XButton,
XTextarea,
Checklist,
Checker,
CheckerItem,
Uploader
},
data() {
return {
projectIds: [],
commonList: [],
httpUrls: [],
declareAddress: "",
declareContent: ""
};
},
methods: {
change(value) {
this.projectIds = value;
},
upload(urls) {
this.httpUrls = urls;
},

getTypes() {
this.$http
.post("/mall/v1/combo/maintenanceProjects", {
buildingCode: "rrrrr"
})
.then(data => {
this.commonList = data.result;
})
.catch(e => {
this.$vux.toast.text(e);
});
},

submit() {
if (this.declareAddress == "") {
this.$vux.toast.text("请填写商铺地址");
return;
}
if (!this.projectIds.length) {
this.$vux.toast.text("请填选择维修项目");
return;
}
this.$http
.post("/mall/v1/declaration/internal", {
declareAddress: this.declareAddress,
declareContent: this.declareContent,
picture: this.httpUrls.join(''),
projectIds: this.projectIds.join(',')
})
.then(data => {
this.$vux.toast.text("提交成功");
this.$router.push({path:'/home/orderList',repalce:true})
})
.catch(e => {
this.$vux.toast.text(e);
});
}
},
created() {
this.getTypes();
}
};



