
import ViewBox from 'vux/src/components/view-box/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import Uploader from "@/common/uploadImg.vue";
export default {
name: "serviceEnd",
components: {
Uploader,
ViewBox,
XTextarea,
Group,
XButton,
XInput
},
data() {
return {
httpUrls: [],
isOk:true,
description:'',
materials: [
{
name: "",
price: "",
quantity: "",
total: ""
}
]
};
},
methods: {
upload(urls) {
this.httpUrls = urls;
},
upload2(urls) {
this.httpUrls2 = urls;
},
add() {
this.materials.push({
name: "",
price: "",
quantity: "",
total: ""
});
},
todelete(index){
this.materials.splice(index,1)
},
submit(){
if(!this.httpUrls.length){
this.$vux.toast.text('请上传维修前照片');
return;
}
if(!this.httpUrls2.length){
this.$vux.toast.text('请上传维修后照片');
return;
}
for(let i=0;i<this.materials.length;i++){
if(this.materials[i].name===""){
this.$vux.toast.text(`请填写材料${i+1}的名称`);
this.isOk = false;
break;
}else{
this.isOk = true;
}
if(this.materials[i].price===""){
this.$vux.toast.text(`请填写材料${i+1}的单价`);
this.isOk = false;
break;
}else{
this.isOk = true;
}
if(this.materials[i].quantity===""){
this.$vux.toast.text(`请填写材料${i+1}的数量`);
this.isOk = false;
break;
}else{
this.isOk = true;
}
if(this.materials[i].total===""){
this.$vux.toast.text(`请填写材料${i+1}的总价`);
this.isOk = false;
break;
}else{
this.isOk = true;
}
}
if(!this.isOk){
return;
}
this.$http.post('/mall/v1/maintenance/repairFinish',{
startImg:this.httpUrls.join(''),
finishImg:this.httpUrls2.join(''),
id:this.$route.query.id,
description:this.description,
materials:this.materials
}).then(data=>{
this.$store.commit('repairEnd',this.$route.query.id)
this.$vux.toast.text('上传成功');
this.$router.go(-1)
}).catch(e => {
this.$vux.toast.text(e);
});
}
},

beforeRouteEnter(t0, from, next) {
next(vm => {
vm.httpUrls=[],
vm.isOk=true,
vm.description='',
vm.materials= [
{
name: "",
price: "",
quantity: "",
total: ""
}
]
});
}
};



