
import XButton from 'vux/src/components/x-button/index.vue'

import { mapState } from "vuex";
import { setTimeout } from "timers";
export default {
components: {
XButton
},
data() {
return {
pageNum: 1,
pageSize: 4,
nodata: false,
topc: 0
};
},
computed: {
...mapState({
list: state => state.order.serviceList
})
},
mounted() {
this.nodata = false;
this.pageNum = 1;
this.$http
.post("/mall/v1/maintenances", {
pageNum: 1,
pageSize: this.pageSize
})
.then(data => {
if (data&&data.result.list) {
this.pageNum = 2;
this.nodata = false;
} else {
this.nodata = true;
}
this.$store.commit("setServiceList", data.result.list || []);
})
.catch(e => {
e && this.$vux.toast.text(e);
});
},
activated() {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.topc, false);
},
methods: {

repairStart(id) {
this.$http
.post("/mall/v1/maintenance/repairStart", { id })
.then(data => {
this.$store.commit("repairStart", id);
})
.catch(e => {
this.$vux.toast.text(e);
});
},

endRepair(id) {
this.$router.push({ path: "/serviceEnd", query: { id } });
},
infinite(done) {
if (this.list.length < this.pageSize) {
done(true);
return;
}
this.$http
.post("/mall/v1/maintenances", {
pageNum: this.pageNum,
pageSize: this.pageSize
})
.then(data => {
done(true);
data.result.list && this.pageNum++;
this.$store.commit("setServiceList", [
...this.list,
...(data.result.list || [])
]);
})
.catch(e => {
done(true);
e && this.$vux.toast.text(e);
});
},

getStauts(status) {
switch (status) {
case 0:
return "待签发";
break;
case 1:
return "待维修";
break;
case 2:
return "维修中";
break;
case 3:
return "已完成";
break;
}
}
},
beforeRouteLeave(to, from, next) {
this.topc = this.$refs.my_scroller.getPosition().top;
next();
}
};



