
import Checklist from 'vux/src/components/checklist/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

export default {
name: "serviceEvaluation",
components: {
Checklist,
ViewBox,
XTextarea,
Group,
XButton,
Checker,
CheckerItem
},
data() {
return {
List1: [
{
key: "好",
value: "好"
},
{
key: "一般",
value: "一般"
},
{
key: "差",
value: "差"
}
],
List2: [
{
key: "迅速",
value: "迅速"
},
{
key: "一般",
value: "一般"
},
{
key: "慢",
value: "慢"
}
],
List3: [
{
key: "好",
value: "好"
},
{
key: "一般",
value: "一般"
},
{
key: "慢",
value: "慢"
}
],
List4: [
{
key: "满意",
value: "满意"
},
{
key: "较满意",
value: "较满意"
},
{
key: "一般",
value: "一般"
},
{
key: "不满意",
value: "不满意"
}
],
value1: "",
value2: "",
value4: "",
suggest: ""
};
},
methods: {
submit() {
this.$http
.post("/mall/v1/evaluation/appraise", {
declarationId: this.$route.query.id,
attitude: this.value1,
satisfaction: this.value4,
timely: this.value2,
suggest: this.suggest
})
.then(data => {
this.$store.commit("changeStatus", this.$route.query.id);
this.$router.go(-1);
})
.catch(e => {
done(true);
this.$vux.toast.text(e);
});
}
},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.value1 = "";
vm.value2 = "";
vm.value4 = "";
vm.suggest = "";
});
}
};



